n\n    &:hover {\n        width: 200px;\n    }\n}\n\n.sidersContent {\n    height: 100%;\n    display: flex;\n    flex-direction: column;\n    padding: 16px 0;\n}\n\n.sidersHeader {\n    padding: 0 12px 16px;\n    border-bottom: 1px solid #333;\n    margin-bottom: 16px;\n}\n\n.logo {\n    display: flex;\n    align-items: center;\n    gap: 12px;\n    height: 40px;\n    overflow: hidden;\n}\n\n.logoIcon {\n    font-size: 24px;\n    flex-shrink: 0;\n    width: 24px;\n    text-align: center;\n}\n\n.logoText {\n    font-size: 18px;\n    font-weight: bold;\n    color: #fff;\n    white-space: nowrap;\n    opacity: 0;\n    transform: translateX(-20px);\n    transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);\n}\n\n.siders:hover .logoText {\n    opacity: 1;\n    transform: translateX(0);\n}\n\n.sidersNav {\n    flex: 1;\n    display: flex;\n    flex-direction: column;\n    gap: 4px;\n    padding: 0 8px;\n}\n\n.sidersItem {\n    display: flex;\n    align-items: center;\n    gap: 12px;\n    padding: 12px;\n    border-radius: 8px;\n    cursor: pointer;\n    transition: all 0.2s ease;\n    overflow: hidden;\n    position: relative;\n\n    &:hover {\n        background: rgba(255, 255, 255, 0.15);\n\n        .sidersItemTitle {\n            margin-left: 10px;\n        }\n    }\n\n    &:active {\n        transform: translateX(2px);\n    }\n\n    &Active {\n        background: rgba(255, 255, 255, 0.15);\n    }\n}\n\n.sidersItemIcon {\n    font-size: 20px;\n    width: 20px;\n    display: flex;\n    align-items: center;\n    justify-content: center;\n    transition: transform 0.2s ease;\n}\n\n.sidersItem:hover .sidersItemIcon {\n    transform: scale(1.1);\n}\n\n.sidersItemTitle {\n    font-size: 14px;\n    color: #fff;\n    white-space: nowrap;\n    opacity: 0;\n    transform: translateX(-20px);\n    transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);\n    font-weight: 500;\n}\n\n.siders:hover .sidersItemTitle {\n    opacity: 1;\n    transform: translateX(0);\n}\n\n.sidersFooter {\n    padding: 16px 8px 0;\n    border-top: 1px solid #333;\n    margin-top: auto;\n}\n\n// \n.sidersItem, .sidersItemActive {\n    &::before {\n        content: '';\n        position: absolute;\n        left: 0;\n        top: 0;\n        bottom: 0;\n        width: 3px;\n        background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);\n        transform: scaleY(0);\n        transition: transform 0.2s ease;\n    }\n\n    &:hover::before {\n        transform: scaleY(1);\n    }\n}\n\n// \n@media (max-width: 768px) {\n    .siders {\n        width: 40px;\n\n        &:hover {\n            width: 180px;\n        }\n    }\n}\n"],"sourceRoot":""}]);
